.App {
 position: relative;
}

i, b {
  font-style: normal;
  font-weight: 400;
}

p {
  margin: 0;
}

button {
  padding: 0;
  border: 0;
  outline: 0;
}
.button {
  display: inline-block;
  text-decoration: none;
  background: #eee;
  color: #777;
  border-radius: 2px;
  padding: 8px 10px;
  font-weight: 400;
  text-align: left;
  margin: 0;
}

.logo {
  width: 100%;
  height: 20px;
  text-align: center;
  margin-top: 40px;
  position: relative;
}

.logo img {
  height: 100%;
  cursor: pointer;
}

.name {
  font-size: 54px;
  margin-top: 30px;
  text-align: center;
  font-weight: 300;
}

.describe {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #888;
}

.describe p {
  margin: 0;
  width: 480px;
  text-align: center;
  margin-top: 20px;
}

.control-wrap {
  width: 100%;
  margin-top: 14px;
}

.control-wrap .control {
  margin: 0 auto;
  width: 400px;
  box-shadow: 0 8px 12px rgba(60,90,120,.1);
  border-radius: 4px;
  color: #666;
  position: relative;
}

.control .create {
  padding: 0 16px;
  text-align: center;
  font-size: 20px;
  background-color: #eee;
  line-height: 39px;
  box-sizing: border-box;
}

.create .key {
  color: #722ed1;
  font-size: 20px;

}

.create .equal {
  color: #1890ff;
  font-size: 20px;
}

.create .module {
  color: #73d13d;
  font-size: 20px;

}

.create .kuo {
  color: #36cfc9;
  font-size: 20px;
}

.control p {
  padding: 0 16px 16px;
  font-size: 14px;
}

.control .first-p {
  padding: 16px;
}

.control p button {
  width: 40px;
  margin-right: 8px;
  text-align: center;
  transition: all 0.2s linear;
  cursor: pointer;
}

.control p button:hover {
  color: #fff;
  background-color: #fa541c;
}

.control p button::before {
  content: '\25b6';
}

.control i {
  color: #999;
  font-size: 16px;
}

.control b {
  color: #1890ff;
  font-weight: 700;
  font-size: 16px;
}

.endword {
  color: #888;
  font-size: 16px;
  text-align: center;
  line-height: 60px;
}

.github {
  width: 100%;
}

.github-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  background-color: #fa541c;
  width: 100px;
  padding: 4px;
  border-radius: 4px;
  cursor: pointer;
  transition: all .4s ease;
}

.github-btn:hover {
  box-shadow: 0 2px 6px rgba(235, 84, 25, 0.3);
}

.github-btn img {
  display: block;
  width: 26px;
  margin-right: 10px;
}

.github-btn p {
  color: #fff;
}

.shadow {
  width: 66px;
  margin: 0 auto;
  border: 1px solid #fa541c;
  border-width: 1px 0 0 0;
  margin-top: 20px;
  box-shadow: 0 -1px 6px rgba(235, 84, 25, 1);
}

.thanks {
  color: #888;
  text-align: center;
  margin: 0 auto;
  max-width: 520px;
  margin-bottom: 10px;
}

a {
  color: #1890ff;
}

.tips {
  color: #eee;
  position: absolute;
  bottom: -22px;
  font-size: 14px;
  text-align: center;
  left: 50%;
  transform: translate(-50%, 0);
  animation: color 4s ease infinite;
}

@keyframes color {
  0% { color: #fa541c; opacity: 0; }
  50% { color: #1890ff; opacity: 0.45; }
  75% { color: #722ed1; opacity: 0.60; }
  100% { color: #fa541c; opacity: 0; }
}